import {Box, SpeedDial, SpeedDialAction, SpeedDialIcon} from "@mui/material";
import ShoppingCartIcon from '@mui/icons-material/ShoppingCart';
import PersonIcon from '@mui/icons-material/Person';
import {useNavigate} from "react-router";
import {useContext} from "react";
import {AppContext} from "../../App";

const actions = [
	{icon: <ShoppingCartIcon />, name: "Shopping Cart", path: "/cart"},
	{icon: <PersonIcon />, name: "Account", path: "/account"}
]
export default function FloatMenu() {

	const navigate = useNavigate();
	const {customer} = useContext(AppContext);

	if (customer === undefined)
		return <></>

	return (
		<Box sx={{ height: 320, transform: 'translateZ(0px)', flexGrow: 1, position: "fixed", right: "1em", bottom: "5em"}}>
			<SpeedDial
				ariaLabel="util"
				sx={{ position: 'absolute', bottom: 16, right: 16 }}
				icon={<SpeedDialIcon />}
			>
				{actions.map((action) => (
					<SpeedDialAction
						onClick={() => navigate(action.path, {replace: true})}
						key={action.name}
						icon={action.icon}
						tooltipTitle={action.name}
					/>
				))}
			</SpeedDial>
		</Box>
	);
}