import {createContext, useEffect, useState} from "react";
import ProductInfo from "./ProductInfo";
import {defaultHeaders} from "../../App";
import Loading from "../../util/Loading";
import {useParams} from "react-router";

type ProductInfoType = {
	loading: boolean
	product: ProductType
	variant?: VariantType
	setVariant: (newVariant?: VariantType) => void
}
// @ts-ignore
export const ProductContext = createContext<ProductInfoType>();

type ProductType = {
	id: number,
	name: string,
	description: string,
	defaultPrice?: number,
	forSaleFrom: string,
	variants?: VariantType[],
	series?: SeriesType[],
	dimensions?: DimensionType[],
	reviews?: ReviewType[],
}

type SeriesType = {
	id: number,
}

type ReviewType = {
	id: number,
	star: number,
	content: string,
	createdAt: string,
	customer?: number,
	product: number
}

export type DimensionType = {
	id: number,
	name: string
}

export type VariantType = {
	id: number,
	singular: boolean,
	extraDescription: string,
	referencePrice: number
	product: number,
	attributes?: AttributeType[],
	mediaFiles: {url: string}[]
	inventories?: InventoryType[]
}

export type AttributeType = {
	id: number,
	value: string
}

export type InventoryType = {
	id: number,
	amount: number
	price: number
	lastModified: string
	merchant: MerchantType
	variant: number
}

type MerchantType = {
	id: number
	email: string,
	brand: string,
	description: string,
	createdAt: string
}

export default function ProductInfoWrapper() {

	const [loading, setLoading] = useState(true);
	const {id} = useParams()
	const [product, setProduct] = useState<ProductType>();
	const [series, setSeries] = useState<SeriesType[]>([]);
	const [variant, setVariant] = useState<VariantType>();

	useEffect(() => {
		fetch(`/api/product/${id}`, {headers: defaultHeaders})
			.then(res => res.json())
			.then(data => {
				setProduct(data);
				setSeries(data["series"]);
				const vars: VariantType[] = data["variants"];
				// we take singular of a variant as: the default variant of a product
				// singular has nothing to do with dimensions
				const defaultVar = vars.filter(v => v.singular);
				if (defaultVar.length === 1) {
					setVariant(defaultVar[0]);
				}
			})
			.then(() => setLoading(false))
			.catch(console.log)
	}, []);

	return (
	// @ts-ignore only assuming that product will not be accessed before the initial fetch
		<ProductContext.Provider value={{loading, product, setVariant, variant}}>
			{!loading && <ProductInfo></ProductInfo>}
			{loading && <Loading></Loading>}
		</ProductContext.Provider>
	);
}