import {AddressType} from "./Address";
import {Accordion, AccordionDetails, AccordionSummary, Box, Container, Divider, Stack, Typography} from "@mui/material";
import ExpandMoreIcon from '@mui/icons-material/ExpandMore';

export type OrderType = {
	id: number,
	createdAt: string,
	status: string,
	totalPrice: number,
	paidPrice: number,
	address: AddressType,
	purchaseItems: {
		amount: number,
		inventory: {
			price: number,
			variant: {
				attributes: {value: string, dimension: {id: number}}[],
				mediaFiles: {url: string}[],
				product: {
					name: string,
					dimensions: {name: string, id: number}
				}
			},
			merchant: {
				brand: string
			}
		}
	}[]
}

export default function Order(order: OrderType) {
	const {id, createdAt, status, totalPrice, paidPrice, address, purchaseItems} = order;
	const date = createdAt.split('T')[0].replaceAll("-", " ");

	return (
		<Box sx={{textAlign: "left", maxWidth: "80vw", padding: "1em 0.2em", marginBottom: "1em"}}>
			<Accordion>
				<AccordionSummary color={"lightgray"} expandIcon={<ExpandMoreIcon />}>
					<Typography variant={"h5"}>{`Order No.${id} placed on ${date} | Status: ${status}`}</Typography>
				</AccordionSummary>
				<Divider color={"darkgray"}></Divider>
				<AccordionDetails>
					<Box sx={{margin: "1em"}}>
						<Typography variant={"h6"}>{`Total price: $${totalPrice}, amount paid: $${paidPrice}`}</Typography>
						<Typography>{`Shipping address: ${address.street}, ${address.postalCode}, ${address.city}\n${address.country}`}</Typography>
					</Box>
						{purchaseItems.map(i => (
							<Box sx={{margin: "1em 3em 1em", height: "32vh", maxHeight: "32vh", maxWidth: "75vw", borderBottom: "0.1em solid lightgray"}}>
								<Stack direction={"row"} alignItems={"stretch"} justifyContent={"flex-start"}>
									<img style={{maxHeight: "28vh", maxWidth: "12vw"}} src={i.inventory.variant.mediaFiles[0].url} />
									<Box sx={{maxWidth: "64vw", width: "64vw", margin: "1em", paddingLeft: "2em"}}>
										<Typography variant={"h6"}>
											{i.inventory.variant.product.name}
										</Typography>
										<Typography>
											{`Amount: ${i.amount}, at $${i.inventory.price} each`}
										</Typography>
										<Typography>
											{`${i.inventory.variant.attributes.map(a => a.value).join(", ")}`}
										</Typography>
										<Typography>
											{`Sold by: ${i.inventory.merchant.brand}`}
										</Typography>
									</Box>
								</Stack>
							</Box>
						))}
				</AccordionDetails>
			</Accordion>
		</Box>
	);
}