import Inventory from "./Inventory";
import Purchase from "./Purchase";
import {useContext, useState} from "react";
import {ProductContext} from "../ProductInfoWrapper";

export default function Stock() {
	const [inventoryId, setInventoryId] = useState(-1);

	return (
		<>
			<Inventory inventoryId={inventoryId} setInventoryId={setInventoryId}></Inventory>
			{inventoryId > 0 && <Purchase inventoryId={inventoryId}></Purchase>}
		</>
	);
}