import {Box, FormControl, FormHelperText, MenuItem, Select} from "@mui/material";
import {useContext, useState} from "react";
import {CatalogContext} from "./CatalogWrapper";

export type SortType = {
	name: string,
	by: string,
	asc: boolean,
}

const options: SortType[] = [
	{name: "Default Ascending", by: "id", asc: true},
	{name: "Default Descending", by: "id", asc: false},
	{name: "Name Ascending", by: "name", asc: true},
	{name: "Name Descending", by: "name", asc: false},
	{name: "Price Ascending", by: "defaultPrice", asc: true},
	{name: "Price Descending", by: "defaultPrice", asc: false},
	{name: "Add Date Ascending", by: "forSaleFrom", asc: true},
	{name: "Add Date Descending", by: "forSaleFrom", asc: false},
]

export default function Sort() {
	const {onSortChange, searching} = useContext(CatalogContext);
	const [sort, setSort] = useState<SortType>(options[0]);

	return (
		<Box height={"6em"} margin={"0.5em 0.2em"}>
			{!searching && <FormControl sx={{m: 1, minWidth: "12em"}}>
				<FormHelperText>Sort By:</FormHelperText>
				<Select
					inputProps={{'aria-label': 'Without label'}}
					value={sort.name}
					onChange={e => {
						const newSort = options.find(op => op.name === e.target.value)!;
						setSort(newSort);
						onSortChange(newSort);
					}}
				>
					{
						options.map((op, index) => <MenuItem value={op.name} key={index}>{op.name}</MenuItem>)
					}
				</Select>
			</FormControl>}
		</Box>
	);
}