import {useContext, useEffect, useState} from "react";
import {AppContext} from "../../../App";
import {Box, Container, TextField, Typography} from "@mui/material";

export default function CustomerWrapper() {
	const {customer} = useContext(AppContext);

	if (customer === undefined)
		return <></>;

	const field = (defaultV: string, label: string, transform: (original: string) => string = (s => s)) => {
		const value = transform(defaultV);
		return (
			<Box sx={{width: "25vw", margin: "2em auto"}}>
				<TextField fullWidth InputProps={{readOnly: true}} defaultValue={value} label={label}>
				</TextField>
			</Box>
		);
	}

	const fields = [[customer.name, "Name"], [customer.email, "Email"], [customer.createdAt, "Creation Date"]];
	return (
		<Container>
			<Typography>Testing account info (non-changeable):</Typography>
			{
				fields.map((v, idx) =>
					idx === 2 ? field(v[0], v[1], x => x.substring(0, x.indexOf("T"))) : field(v[0], v[1])
				)
			}
		</Container>
	);
}