import React, {createContext, useEffect, useState} from 'react';
import './App.css';
import Header from "./header/Header";
import Footer from "./footer/Footer";
import ProductInfoWrapper from "./main/product/ProductInfoWrapper";
import AccountWrapper from "./main/customer/AccountWrapper";
import CatalogWrapper from "./main/catalog/CatalogWrapper";
import {Route, Routes} from "react-router";
import Login from "./main/security/Login";
import {Box} from "@mui/material";
import Basket from "./main/transaction/Basket";
import Checkout from "./main/transaction/Checkout";
import Home from "./main/Home";

export const defaultHeaders = {"Content-Type": "application/json"};

type Customer = {
	name: string,
	email: string,
	createdAt: string,
}

type AppContextType = {
	customer: Customer | undefined,
	setCustomer: (customer: Customer | undefined) => void
}
export const AppContext = createContext<AppContextType>({customer: undefined, setCustomer: () => {}});

export const refreshCustomer = (setCustomer: (customer: Customer | undefined) => void, sideEffect: () => void) => {
	fetch("/api/customer/session", {headers: defaultHeaders})
		.then(res => res.json())
		.then(value => {
			setCustomer(value);
		})
		.catch(() => setCustomer(undefined))
		.finally(() => sideEffect());
}
function App() {

	const [customer, setCustomer] = useState<Customer | undefined>(undefined);

	useEffect(() => {
		refreshCustomer(setCustomer, () => {});
	}, [])

	return (
		<AppContext.Provider value={{customer, setCustomer}}>
			<div className={"App"}>
				<Header></Header>
				<Box sx={{minHeight: "75vh"}}>
					<Routes>
						<Route path={"/"} element={<Home/>} />
						<Route path={"/catalog"} element={<CatalogWrapper/>} />
						<Route path={"/product/:id"} element={<ProductInfoWrapper/>} />
						<Route path={"/account"} element={<AccountWrapper></AccountWrapper>} />
						<Route path={"/login"} element={<Login></Login>}/>
						<Route path={"/basket"} element={<Basket></Basket>} />
						<Route path={"/checkout"} element={<Checkout></Checkout>} />
					</Routes>
				</Box>
				<Footer></Footer>
			</div>
		</AppContext.Provider>
	);
}

export default App;
