import {createContext, useContext, useEffect, useState} from "react";
import {AppContext, defaultHeaders} from "../../App";
import {Box, Button, Container, Typography} from "@mui/material";
import Grid from "@mui/material/Unstable_Grid2";
import Loading from "../../util/Loading";
import BasketItem, {BasketItemType} from "./BasketItem";
import Checkout from "./Checkout";
import {useNavigate} from "react-router";
import {Link} from "react-router-dom";

type BasketType = {
	id: number,
	lastModified: string,
	totalPrice: number,
}
type Updater = (f: (x: number) => number) => void
export const BasketContext = createContext<Updater>(x => x);

export default function Basket() {
	const {customer} = useContext(AppContext);
	const [basket, setBasket] = useState<BasketType | undefined>(undefined);
	const [items, setItems] = useState<BasketItemType[]>([]);
	const [loading, setLoading] = useState(true);
	const [update, setUpdate] = useState(0);

	const navigate = useNavigate();

	useEffect(() => {
		if (customer === undefined) {
			navigate('/login')
		}
		fetch('/api/basket',
			{headers: defaultHeaders, method: "GET"})
			.then(res => res.json())
			.then(data => {
				setBasket(data);
				setItems(data["basketItems"]);
			})
			.catch(console.log)
			.finally(() => setLoading(false))
	}, [update]);

	if (customer === undefined)
		navigate("/login");

	if (loading)
		return <Loading height={"20vh"}></Loading>

	return (
		<BasketContext.Provider value={setUpdate}>
			<Container sx={{width: "80vw", marginTop: "2em"}}>
				<Grid columns={8} container>
					<Grid xs={6}>
						{items.length === 0 &&
                            <Box>
                                <Typography variant={"h6"}>No item in your basket yet</Typography>
                                <Link to={"/catalog"}><Typography>Browse products</Typography></Link>
                            </Box>
						}
						{
							items.map(i => <BasketItem {...i} />)
						}
					</Grid>
					<Grid xs={2}>
						<Container>
							<Typography variant={"h5"} color={""}>Total: ${basket!.totalPrice.toFixed(2)}</Typography>
							{items.length > 0 && <Box>
                                <Button size={"large"} sx={{textTransform: "none"}} onClick={e => {
									e.preventDefault();
									navigate('/checkout')
								}}>
                                    To checkout
                                </Button>
                            </Box>}
						</Container>
					</Grid>
				</Grid>
			</Container>
		</BasketContext.Provider>

	);
}