import Paper from '@mui/material/Paper';
import IconButton from '@mui/material/IconButton';
import SearchIcon from '@mui/icons-material/Search';
import "./SearchFunction.css"
import SearchBar from "./SearchBar"
import {useContext} from "react";
import {CatalogContext} from "../CatalogWrapper";

function SearchFunction() {
	const {onSearch, searchRef} = useContext(CatalogContext);
	// const textInput = useRef<HTMLInputElement>(null);
	return (
		<Paper
			component="div" elevation={0} variant={"outlined"}
			sx={{ display: 'flex', alignItems: 'center', marginTop: '1.25em', marginLeft: '2em'}}
		>
			<SearchBar></SearchBar>
			<IconButton onClick={e => {
				e.preventDefault();
				const searchKey = searchRef.current?.querySelector("input")?.value || "";
				onSearch(searchKey);
			}} className={"search-button"} type="button" sx={{ p: '10px' }} aria-label="search">
				<SearchIcon />
			</IconButton>
		</Paper>
	);
}

export default SearchFunction;