import {useContext, useState} from "react";
import {Container, Pagination, Stack, Typography} from "@mui/material";
import {CatalogContext} from "../CatalogWrapper";

export default function Page() {
	const {totalElementAmount, pageAmount, onPageNumberChange, pageNumber} = useContext(CatalogContext);
	const [page, setPage] = useState(pageNumber);

	return (
		<Stack spacing={2} padding={"2em"}>
			<Typography textAlign={"right"} marginRight={"10vw"}>
				{`Products found: ${totalElementAmount}`}
				{pageAmount > 1 && ` | Current page: ${page}`}
			</Typography>
			{pageAmount > 1 && <Container>
                <Pagination sx={{width: "40vw !important", margin: "auto"}} size={"large"} count={pageAmount}
                            page={page}
                            onChange={(ev, value) => {
								setPage(value);
								onPageNumberChange(value);
							}}/>
            </Container>}
		</Stack>
	);
}