import {Button, Box} from "@mui/material";
import PersonIcon from '@mui/icons-material/Person';
import {useContext} from "react";
import {AppContext} from "../App";
import {useNavigate} from "react-router";

function Account() {
	const navigate = useNavigate();
	const {customer} = useContext(AppContext);
	if (customer === undefined)
		return (
			<Box>
				<Button sx={{padding: "0.5em 1em", textTransform: "none", fontSize: "large"}} color={"secondary"} variant={"contained"}
				onClick={e => {
					e.preventDefault();
					navigate('/login');
				}}>
					Login
				</Button>
			</Box>
		);

	return (
		<Box>
			<Button sx={{padding: "0.5em", textTransform: "none", fontSize: "large"}}
					color={"secondary"} variant={"contained"} startIcon={<PersonIcon />}
					onClick={e => {
						e.preventDefault();
						navigate('/account');
			}}>
				Account
			</Button>
		</Box>
	);
}

export default Account;