import "./Header.css"
import Logo from "./Logo"
import Grid from "@mui/material/Unstable_Grid2";
import Account from "./Account"
import "./Header.css"
import NavigationBar, {NavigationBarType} from "./NavigationBar";
import {useContext} from "react";
import {AppContext} from "../App";
import {Stack, Typography} from "@mui/material";

const nav: NavigationBarType[] = [
	{menu: {name: "All Products", link: "/catalog"}, login: false},
	{
		menu: {name: "Readme", link: "https://hubpages.com/technology/Webshop-Development"},
		login: false
		// subMenu: [
		// 	{name: "contact", link: "http://google.com"},
		// 	{name: "how it works", link: "http://google.com"}
		// ],
	},
	{menu: {name: "Code", link: "https://github.com/Qrzlo/webshop"}, login: false},
	{menu: {name: "Basket", link: "/basket"}, login: true}
]

function Header() {
	const {customer} = useContext(AppContext);
	const loggedIn = customer !== undefined;

	return (
		<Grid columns={12} justifyContent={"center"} alignItems={"center"} className={"header"} container borderColor={"black"}>
			<Grid xs={0.5}></Grid>
			<Grid xs={1}><Logo></Logo></Grid>
			<Grid xs={1}><Typography variant={"h5"}>Webshop</Typography></Grid>
			<Grid xs={0.5}></Grid>
			<Grid xs={6}>
				<Stack direction={"row"} justifyContent={"space-evenly"} alignItems={"center"} spacing={0}>
					{nav.filter(n => loggedIn || !n.login).map(e => <NavigationBar login={e.login} menu={e.menu} subMenu={e.subMenu} />)}
				</Stack>
			</Grid>
			<Grid xs={1}></Grid>
			<Grid xs={1.5}><Account/></Grid>
			<Grid xs={0.5}></Grid>
		</Grid>
	);
}

export default Header;