import {Box, FormControl, FormHelperText, MenuItem, Select} from "@mui/material";
import {useContext, useState} from "react";
import {CatalogContext} from "../CatalogWrapper";

const sizes = [20, 40, 100];

export default function Size() {
	const [size, setSize] = useState(sizes[0]);
	const {onPageSizeChange, searching} = useContext(CatalogContext);

	return (
		<Box height={"6em"} margin={"0.5em 0.2em"}>
			{!searching && <FormControl sx={{m: 1, minWidth: "8em"}}>
				<FormHelperText>{`Show ${size} per page:`}</FormHelperText>
				<Select
					inputProps={{'aria-label': 'Without label'}}
					value={size}
					onChange={e => {
						const newSize = parseInt(`${e.target.value}`);
						setSize(newSize);
						onPageSizeChange(newSize);
					}}
				>
					{
						sizes.map((sz, index) => <MenuItem value={sz} key={index}>{sz}</MenuItem>)
					}
				</Select>
			</FormControl>}
		</Box>
	);


}