import {createContext, useRef, useState} from "react";
import Catalog from "./Catalog";
import {ProductType} from "./Product";
import {base, bookBase, CategoryType} from "./category/Category";
import {SortType} from "./Sort";
import {defaultHeaders} from "../../App";

// keyword + category
// category + size + page + order + dir

export type CatalogType = {
	products: ProductType[],//, setProducts: (newProducts: ProductType[]) => void,
	category: number, onCategoryChange: (category: CategoryType) => void, //setCategory: React.Dispatch<React.SetStateAction<number>>,
	pageSize: number, onPageSizeChange: (size: number) => void,
	pageNumber: number, onPageNumberChange: (page: number) => void,
	pageAmount: number, totalElementAmount: number,
	sort: {by: string, asc: boolean}, onSortChange: (newSort: SortType) => void,
	loading: boolean, searching: boolean, setSearching: (newSearching: boolean) => void, searchRef: React.RefObject<HTMLDivElement>,
	onSearch: (key: string) => void
}

// @ts-ignore
export const CatalogContext = createContext<CatalogType>();

// the products/setProducts will be used by sort, search and category to change the view of products
export default function CatalogWrapper() {
	const [products, setProducts] = useState<ProductType[]>([]);
	const [category, setCategory] = useState<number>(base.id);
	const [pageSize, setPageSize] = useState(20);
	const [pageNumber, setPageNumber] = useState(1);
	const [pageAmount, setPageAmount] = useState(0);
	const [totalElementAmount, setTotalElementAmount] = useState(0);
	const [sort, setSort] = useState({by: "id", asc: true});
	const [loading, setLoading] = useState(true);
	const [searching, setSearching] = useState(false);
	const searchRef = useRef<HTMLDivElement>(null);

	const refresh = (categoryId: number, pageSize: number, pageNumber: number, sortBy: string, dir: boolean) => {
		setSearching(false);
		setLoading(true);
		const base = `/api/product?`;
		const url = base + new URLSearchParams({
			"category": categoryId.toString(),
			"size": pageSize.toString(),
			"page": pageNumber.toString(),
			"order": sortBy,
			"dir": dir ? "asc" : "desc",
		});
		fetch(url, {headers: defaultHeaders})
			.then(res => res.json())
			.then(data => {
				setProducts(data["content"])
				setPageAmount(data["totalPages"])
				setTotalElementAmount(data["totalElements"])
			})
			.catch(console.log)
			.finally(() => setLoading(false))
	}

	const onSearch = (keyword: string, newCategory?: number) => {
		if (keyword === "") return;
		setLoading(true);
		setSearching(true);
		fetch(`/api/product/search?key=${encodeURIComponent(keyword)}` + (category > bookBase.id ? `&category=${newCategory ?? category}` : ''),
			{headers: defaultHeaders})
			.then(res => res.json())
			.then(data => {
				setTotalElementAmount(data["totalElements"]);
				setProducts(data["content"]);
			})
			.finally(() => {
				setSearching(true)
				setLoading(false);
			})
	}

	const onSortChange = (newSort: SortType) => {
		setSort({by: newSort.by, asc: newSort.asc});
		refresh(category, pageSize, pageNumber, newSort.by, newSort.asc);
	}

	const onPageSizeChange = (newSize: number) => {
		setPageSize(newSize);
		if (pageAmount <= 1 && newSize >= pageSize)
			return;
		setPageNumber(1);
		refresh(category, newSize, 1, sort.by, sort.asc);
	}

	const onPageNumberChange = (newPage: number) => {
		setPageNumber(newPage);
		refresh(category, pageSize, newPage, sort.by, sort.asc);
	}

	const onCategoryChange = (newCategory: CategoryType) => {
		setCategory(newCategory.id);
		if (!searching) {
			setPageNumber(1);
			refresh(newCategory.id, pageSize, 1, sort.by, sort.asc);
		}
		else {
			const inputEl = searchRef.current?.querySelector("input") as HTMLInputElement;
			onSearch(inputEl.value, newCategory.id);
		}
	}

	return (
		<CatalogContext.Provider value={{ totalElementAmount, pageAmount, products, loading,
			sort, onSortChange, category, onCategoryChange, pageSize, onPageSizeChange, pageNumber, onPageNumberChange,
			 onSearch, searching, setSearching, searchRef}}>
			<Catalog></Catalog>
		</CatalogContext.Provider>
	);
}