import {useContext} from "react";
import {ProductContext} from "../ProductInfoWrapper";
import {Box, Rating, Typography} from "@mui/material";
import StarRoundedIcon from '@mui/icons-material/StarRounded';
import StarBorderRoundedIcon from '@mui/icons-material/StarBorderRounded';
import StarHalfRoundedIcon from '@mui/icons-material/StarHalfRounded';
import {grey} from "@mui/material/colors";

export default function RatingWrapper() {
	const {product} = useContext(ProductContext);
	const reviews = product.reviews;

	if (!reviews || reviews.length === 0)
		return <Box><Typography>No rating</Typography></Box>

	const stars = reviews.map(r => r.star);
	const avg = stars.reduce((acc, star) => star + acc, 0) / stars.length;

	return (
		<Box padding={"0.5em"}>
			<Rating readOnly value={avg}></Rating>
			<Typography variant={"subtitle2"} color={grey.A700}>{`${avg} out of 5`}</Typography>
		</Box>
	);
}

