import {Box, Container, Stack} from "@mui/material";
import loadingSVG from "../main/catalog/loading2.svg";

type LoadingType = {
	height?: string
}

export default function Loading({height = "20vh"}: LoadingType) {

	return (
		<Container sx={{margin: "2em", height: "100%"}}>
			<Box>
				<Stack direction={"column"} justifyContent={"center"}>
					<Box sx={{height: height}}><div></div></Box>
					<Box>
						<img style={{height: "100%", maxWidth: "inherit"}} src={loadingSVG}/>
					</Box>
					<Box sx={{height: height}}><div></div></Box>
				</Stack>
			</Box>
		</Container>
	);
}