import {Box, Button, FormControl, FormHelperText, MenuItem, Select, Stack, Typography} from "@mui/material";
import {useContext, useState} from "react";
import {ProductContext} from "../ProductInfoWrapper";
import {AppContext, defaultHeaders} from "../../../App";
import {useNavigate} from "react-router";

type PurchaseType = {
	inventoryId: number
}
export default function Purchase({inventoryId}: PurchaseType) {
	const {customer} = useContext(AppContext);
	const {variant} = useContext(ProductContext);
	const [quantity, setQuantity] = useState<number>(0);
	const [modified, setModified] = useState(false);
	const [sending, setSending] = useState(false);
	const [error, setError] = useState("");
	const navigate = useNavigate();

	if (!variant)
		return <></>

	return (
		<Box marginTop={"2em"}>
			<FormControl size={"small"} sx={{width: "50%", marginBottom: "1em"}}>
				<Stack direction={"row"} justifyContent={"center"} spacing={1} alignItems={"center"}>
					<Box><Typography variant={"subtitle1"}>{`Quantity:`}</Typography></Box>
					<Select value={quantity} onChange={e => setQuantity(parseInt(`${e.target.value}`))}>
						{Array.from(Array(10)).map((v, idx) => {
							return <MenuItem key={idx} value={idx}>{idx}</MenuItem>
						})}
					</Select>
				</Stack>
			</FormControl>
			<Box>
				{error && <Typography>{error}</Typography>}
				<Button sx={{textTransform: "none"}} variant={"contained"} color={"warning"}
						 disabled={sending || modified || error !== ""}
						 onClick={() => {
							 if (quantity === 0)
								 return;
							 if (customer === undefined) {
								 navigate('/login');
								 return;
							 }
							 setSending(true);
							 fetch('/api/basketitem',
								 {
									 headers: defaultHeaders, method: "POST",
									 body: JSON.stringify({inventory: {id: inventoryId}, amount: quantity})
								 })
								 .then(res => {
									 if (res.status === 200) {
										 setModified(true);
									 } else {
										 setError("Something went wrong... Please try again or later")
									 }
								 })
								 .finally(() => setSending(false));
						 }}>
					{modified ? `Item Added` : `Add To Basket`}
				</Button>
			</Box>

		</Box>
	);
}