import {useContext} from "react";
import {ProductContext} from "../ProductInfoWrapper";
import {Box, Typography} from "@mui/material";
import {grey} from "@mui/material/colors";

export default function Variant() {
	const {product, variant} = useContext(ProductContext);

	return (
		<Box marginTop={"1em"} textAlign={"left"}>
			{variant && <Typography>{variant.extraDescription}</Typography>}
			<Typography variant={"h5"} color={grey.A700}>Product description: </Typography>
			<Typography dangerouslySetInnerHTML={{__html: product.description}}>
			</Typography>
		</Box>
	);
}
