import {Alert, Box, Button, Container, Divider, FormControl, MenuItem, Select, Stack, TextField} from "@mui/material";
import countries from "./country.json";
import {useState} from "react";
import {defaultHeaders} from "../../../App";
import Grid from "@mui/material/Unstable_Grid2";
import {useNavigate} from "react-router";

export type AddressType = {
	id: number,
	street: string,
	city: string,
	postalCode: string,
	country: string
}

type AddressPropType = {
	address: AddressType,
	old: boolean,
	setEdit: (f: (x: number) => number) => void
}

const valid = (fields: string[]) => {
	return !fields.some(f => f.length === 0)
}

export default function Address({address: {id, street, city, postalCode, country}, old, setEdit}: AddressPropType) {
	const [newStreet, setNewStreet] = useState(street);
	const [newCity, setNewCity] = useState(city);
	const [newPostalCode, setNewPostalCode] = useState(postalCode);
	const [newCountry, setNewCountry] = useState(country);
	const [sending, setSending] = useState(false);
	const [status, setStatus] = useState(-1);
	const [body, setBody] = useState("");

	const createFetchBody = (id?: number) => {
		const body = {
			street: newStreet,
			city: newCity,
			country: newCountry,
			postalCode: newPostalCode
		}
		return id ? {...body, id} : body;
	}

	return (
		<Container sx={{margin: "1em"}}>
			<FormControl sx={{'& > :not(style)': {width: '60vw'}}} disabled={sending}>
				<Grid container columns={10}>
					<Grid xs={5}><TextField required sx={{width: "80%", margin: "0.5em 1em"}} onChange={e => setNewStreet(e.target.value as string)} label={"street"} defaultValue={newStreet}/></Grid>
					<Grid xs={5}><TextField required sx={{width: "80%", margin: "0.5em 1em"}} onChange={e => setNewCity(e.target.value as string)} label={"city"} defaultValue={newCity}/></Grid>
					<Grid xs={5}><TextField required sx={{width: "80%", margin: "0.5em 1em"}} onChange={e => setNewPostalCode(e.target.value as string)} label={"postalCode"} defaultValue={newPostalCode}/></Grid>
					<Grid xs={5}><TextField required sx={{width: "80%", margin: "0.5em 1em"}} onChange={e => setNewCountry(e.target.value as string)} label={"country"} value={newCountry} select>
						{countries.map(c => <MenuItem key={c.code} value={c.code}>{c.name}</MenuItem>)}
					</TextField>
					</Grid>
				</Grid>
			</FormControl>
			<Stack sx={{width: "80%", margin: "1em"}} direction={"row"} spacing={2} alignItems={"center"} justifyContent={"flex-end"}>
				{old && (<>
					<Button variant={"contained"} disabled={sending} onClick={e => {
						e.preventDefault();
						if (!valid([newStreet, newCity, newCountry, newPostalCode]))
							return ;
						setSending(true);
						fetch('/api/address',
							{
								headers: defaultHeaders, method: "PUT",
								body: JSON.stringify(createFetchBody(id))
							})
							.then(res => {
								setStatus(res.status);
								res.json()
									.then((data) => {
										if (res.status === 200)
											setEdit(x => x + 1)
										else
											setBody(data["reason"])
									})
									.catch(setBody);
							})
							.catch(setBody)
							.finally(() => {
								setSending(false);
							});
					}}>
						Save
					</Button>
					<Button variant={"contained"} disabled={sending} onClick={e => {
						e.preventDefault();
						setSending(true);
						fetch(`/api/address/${id}`,{
								headers: defaultHeaders, method: "DELETE"
							})
							.then(res => {
								setStatus(res.status);
								res.json()
									.then((data) => {
										if (res.status === 200)
											setEdit(x => x + 1)
										else
											setBody(data["reason"])
									})
									.catch(setBody);
							})
							.catch(setBody)
							.finally(() => {
								setSending(false);
							});
					}}>
						Delete
					</Button>
				</>)
				}
				{!old &&
                    <Button variant={"contained"} disabled={sending} onClick={e => {
						e.preventDefault();
						if (!valid([newStreet, newCity, newCountry, newPostalCode]))
							return ;
						setSending(true);
						fetch('/api/address',
							{
								headers: defaultHeaders,
								method: "POST",
								body: JSON.stringify(createFetchBody())
							})
							.then(res => {
								setStatus(res.status);
								res.json()
									.then((data) => {
										if (res.status > 299)
											setBody(data["reason"]);
										else {
											setEdit(x => x + 1);
											setNewCity("");
											setNewStreet("");
											setNewCountry("");
											setNewPostalCode("");
										}
									})
									.catch(setBody);
							})
							.catch(setBody)
							.finally(() => {
								setSending(false);
							})
					}}>
                        Create
                    </Button>
				}
			</Stack>
			{status > 0 &&
				(status === 200 || status === 201 ?
				<Alert severity={"success"}>Success</Alert> :
				<Alert severity={"error"}>Something went wrong. {body?.toString()}</Alert>)
			}
		</Container>

	);
}