import {Box, Container, Tab, Tabs, Typography} from "@mui/material";
import {useContext, useState} from "react";
import {AppContext} from "../../App";
import AddressWrapper from "./account/AddressWrapper";
import CustomerWrapper from "./account/CustomerWrapper";
import OrderWrapper from "./account/OrderWrapper";
import {useNavigate} from "react-router";

interface TabPanelProps {
	children?: React.ReactNode;
	index: number;
	value: number;
}
function TabPanel(props: TabPanelProps) {
	const { children, value, index } = props;

	return (
		<div
			role="tabpanel"
			hidden={value !== index}
			id={`simple-tabpanel-${index}`}
			aria-labelledby={`simple-tab-${index}`}
		>
			{value === index && (
				<Box sx={{ p: 3, minHeight: "80vh" }}>
					{children}
				</Box>
			)}
		</div>
	);
}

export default function AccountWrapper() {
	const {customer} = useContext(AppContext);
	const [tab, setTab] = useState(0);
	const navigate = useNavigate();

	if (customer === undefined)
		navigate('/login');

	return (
		<Container>
			<Box sx={{width: "80vw", marginTop: "2em"}}>
				<Box sx={{width: "100%", borderBottom: "0.1em solid", borderColor: "divider"}}>
					<Tabs centered value={tab} textColor={"secondary"}
						  onChange={(e, v) => setTab(v)}>
						{["Account", "Address", "Order"].map(v => <Tab label={v} sx={{textTransform: "none", fontSize: "larger"}} />)}
					</Tabs>
				</Box>
				<TabPanel index={0} value={tab}>
					<CustomerWrapper></CustomerWrapper>
				</TabPanel>
				<TabPanel index={1} value={tab}>
					<AddressWrapper></AddressWrapper>
				</TabPanel>
				<TabPanel index={2} value={tab}>
					<OrderWrapper></OrderWrapper>
				</TabPanel>
			</Box>
		</Container>
	);
}