import "./Catalog.css"
import Grid from "@mui/material/Unstable_Grid2";
import Category from "./category/Category"
import {Box, Button, Divider} from "@mui/material";
import Product from "./Product";
import Sort from "./Sort";
import FloatMenu from "../../header/dial/FloatMenu";
import Page from "./frame/Page";
import Size from "./frame/Size";
import {useContext} from "react";
import {CatalogContext} from "./CatalogWrapper";
import SearchFunction from "./search/SearchFunction";
import Loading from "../../util/Loading";

export default function Catalog() {
	const {products, loading, setSearching, searching, searchRef} = useContext(CatalogContext);

	return (
		<Grid columns={10} container className={"home"}>
			<Grid xs={1.9}>
				<Category></Category>
			</Grid>
			<Grid padding={"1em 0"} xs={0.1}>
				<Divider orientation={"vertical"}></Divider>
			</Grid>
			<Grid xs={8}>
				<Grid justifyContent={"center"} alignItems={"center"} columns={10} container>
					<Grid xs={6}><SearchFunction></SearchFunction></Grid>
					<Grid xs={0.25}></Grid>
					{!searching &&
						<>
                            <Grid xs={1.75}><Sort></Sort></Grid>
                            <Grid xs={0.25}></Grid>
                            <Grid xs={1.5}><Size></Size></Grid>
						</>
					}
					{searching &&
						<>
                            <Grid xs={1.5} margin={"2.5em auto 1em"}>
                                <Box>
                                    <Button sx={{maxHeight: "100%", maxWidth: "100%", margin: "auto", textTransform: "none", fontWeight: "bold", fontSize: "large"}}
                                            onClick={e => {
												setSearching(false);
												const inputEl = searchRef.current?.querySelector("input") as HTMLInputElement //.querySelector("input")
												if (inputEl) {
													inputEl.value = "";
												}
											}}>
                                        Exit Search
                                    </Button>
                                </Box>
                            </Grid>
                            <Grid xs={2}></Grid>
						</>
					}
					<Grid xs={0.25}></Grid>
				</Grid>
				<Divider style={{padding: "0.2em 1em"}}></Divider>
				<Grid columns={9} minHeight={"50vh"} container>
					{loading &&
						<Grid xs={8}>
							<Loading height={"20vh"}></Loading>
						</Grid>}
					{!loading && products.map((p, index) => (<Grid key={index} xs={2}><Product {...p}></Product></Grid>))}
					<Grid xs={1}><FloatMenu/></Grid>
				</Grid>
				{!loading && !searching && <Page></Page>}
			</Grid>
		</Grid>
	);
}