import {Alert, Box, Button, Container, MenuItem, Select, Stack, Typography} from "@mui/material";
import Grid from "@mui/material/Unstable_Grid2";
import NoImg from "../../logo.svg";
import {defaultHeaders} from "../../App";
import {useContext, useState} from "react";
import {BasketContext} from "./Basket";

export type BasketItemType = {
	updater: (x: number) => number,
	id: number,
	amount: number,
	addedAt: string,
	inventory: {
		id: number,
		amount: number
		price: number
		lastModified: string
		variant: {
			id: number,
			singular: boolean,
			extraDescription: string,
			referencePrice: number
			product: {
				id: number,
				name: string,
				description: string,
				defaultPrice?: number,
				forSaleFrom: string,
			},
			attributes: {id: number, value: string, dimension: {name: string}}[],
			mediaFiles: {url: string}[]
		}
	}
}

export default function BasketItem(item: BasketItemType) {
	const [sending, setSending] = useState(false);
	const [body, setBody] = useState("");

	const {id, amount, inventory} = item;
	const preview = inventory.variant.mediaFiles[0].url ?? NoImg;
	const noStock = inventory.amount < amount;
	const updater = useContext(BasketContext);

	return (
		<Box sx={{height: "36vh", maxWidth: "100%", maxHeight: "36vh"}}>
			<Grid columns={12} sx={{maxHeight: "100%"}} container>
				<Grid xs={3} sx={{maxHeight: "80%"}}>
					<Container>
						<img src={preview} style={{maxWidth: "12vw", maxHeight: "24vh"}} />
					</Container>
				</Grid>
				<Grid xs={1}></Grid>
				<Grid xs={4} sx={{maxHeight: "80%"}}>
					<Box textAlign={"left"}>
						<Typography variant={"h6"}>{inventory.variant.product.name}</Typography>
						<Typography>
							{inventory.variant.attributes
								.map(a => `${a.dimension.name}: ${a.value}`)
								.join("; ")
							}
						</Typography>
					</Box>
					{noStock && <Typography color={"red"}>This variant has less stock than you have selected.</Typography>}
					<Stack sx={{marginTop: "0.5em"}} direction={"row"} alignItems={"center"} justifyContent={"flex-start"}>
						<Typography sx={{width: "5vw"}}>Amount: </Typography>
						<Select
							size={"small"}
							disabled={sending}
							defaultValue={amount}
							onChange={e => {
								const newAmount = e.target.value as number;
								if (newAmount < 1)
									return;
								setSending(true);
								fetch('/api/basketitem',
									{
										headers: defaultHeaders, method: "PUT", body: JSON.stringify({
											id, amount: newAmount, inventory: {id: inventory.id}
										})
									})
									.then(res => {
										if (res.status === 200) {
											updater(x => x + 1);
										}
										else {
											res.json().then(data => setBody(data["reason"]))
										}
									})
									.finally(() => setSending(false));

							}}>
							{Array.from(Array(Math.min(inventory.amount, 10))).map((_, idx) => <MenuItem value={1 + idx}>{1 + idx}</MenuItem>)}
						</Select>
					</Stack>
					{body.length > 0 &&
                        <Alert severity={"error"}>Something went wrong: {body?.toString()}</Alert>
					}
				</Grid>
				<Grid xs={3} sx={{maxHeight: "80%"}}>
					<Typography variant={"h6"} color={"darkred"}>{`$${inventory.price * amount}`}</Typography>
					<Button disabled={sending} sx={{textTransform: "none"}} onClick={e => {
						e.preventDefault();
						setSending(true);
						fetch('/api/basketitem',
							{headers: defaultHeaders, method: "DELETE", body: JSON.stringify({id: id})
							})
							.then(res => {
								if (res.status === 200) {
									updater(x => x + 1);
								}
								else {
									res.json().then(data => {
										setBody(data["reason"]);
									})
								}
							})
							.finally(() => setSending(false));
					}}>
						Delete
					</Button>
				</Grid>
			</Grid>
		</Box>
	);
}