import {
	Alert,
	Box,
	Button,
	Container,
	Stack,
	Step,
	StepLabel,
	Stepper,
	StepperContext,
	Typography
} from "@mui/material";
import {useEffect, useState} from "react";
import {defaultHeaders} from "../../App";
import Loading from "../../util/Loading";
import {BasketItemType} from "./BasketItem";
import {DimensionType} from "../product/ProductInfoWrapper";
import {AddressType} from "../customer/account/Address";
import Grid from "@mui/material/Unstable_Grid2";
import {useNavigate} from "react-router";
import {Link} from "react-router-dom";

type PurchaseType = {
	id: number,
	address?: { id: number },
	totalPrice: number,
	paidPrice: number,
	purchaseItems: PurchaseItemType[]
}
type PurchaseItemType = {
	purchase: { id: number },
	amount: number,
	inventory: {
		id: number,
		price: number,
		variant: {
			id: number,
			singular: boolean,
			extraDescription: string,
			product: {
				id: number,
				name: string
			},
			attributes: {id: number, value: string, dimension: {name: string }}[],
			mediaFiles: { url: string }[]
		}
	}
}

enum StepEnum {items, address, review, success}
const steps = ["Order items", "Select address", "Review", "Success"]

export default function Checkout() {
	const [activeStep, setActiveStep] = useState<number>(-1);
	const [initializationError, setInitializationError] = useState("");
	const [orderError, setOrderError] = useState("");
	const [initializedPurchase, setInitializedPurchase] = useState<PurchaseType>();
	const [purchaseItems, setPurchaseItems] = useState<PurchaseItemType[]>([]);
	const [address, setAddress] = useState<number>(-1);
	const [addresses, setAddresses] = useState<AddressType[]>([]);
	const [blocking, setBlocking] = useState(true);
	const navigate = useNavigate();

	// data initilization
	useEffect(() => {
		setBlocking(true);
		const fetchBasketItemsAndAddresses = () => {
			fetch('/api/basket',
				{headers: defaultHeaders, method: "GET"})
				.then(res => res.json())
				.then(data => {
					const items: PurchaseItemType[] = data["basketItems"];
					console.log(data);
					setPurchaseItems(items); // the other useEffect will be invoked
				})
				.then(() => {
					setBlocking(initializedPurchase === undefined || addresses.length === 0);
				})
				.catch(err => setInitializationError(err))

			fetch('/api/address',
				{headers: defaultHeaders, method: "GET"})
				.then(res => res.json())
				.then((data: AddressType[]) => {
					// if there is no address?
					console.log(data);
					if (data.length === 0) {

					}
					setAddresses(data);
				})
				.then(() => {
					setBlocking(initializedPurchase === undefined || purchaseItems.length === 0);
				})
		}
		fetch('/api/purchase/initialize',
			{headers: defaultHeaders, method: "GET"})
			.then(res => res.json())
			.then(data => {
				console.log(data);
				setInitializedPurchase(data);
				fetchBasketItemsAndAddresses();
				setActiveStep(0);
			})

	}, []);

	useEffect(() => {
		setBlocking(true);
		if (purchaseItems.length === 0 || initializedPurchase === undefined)
			return ;
		fetch('/api/purchaseitem',
			{
					headers: defaultHeaders,
					method: "POST",
					body: JSON.stringify(purchaseItems.map(i =>
						({amount: i.amount, inventory: {id: i.inventory.id}})
					))
				}
			)
			.then(res => {
				if (res.status > 299) {
					console.log(res.status);
				}
				setBlocking(false);
			})
	}, [purchaseItems])

	if (initializationError.length > 0)
		return <Alert severity={"error"}>Error: {initializationError}</Alert>

	if (initializedPurchase === undefined) {
		return (
			<Container sx={{margin: "2em"}}>
				<Typography variant={"h5"}>Initializing...</Typography>
				<Loading height={"30vh"}></Loading>
			</Container>
		);
	}

	const refresh = () => {
		setBlocking(true);
		fetch('/api/purchase/current', {headers: defaultHeaders, method: "GET"})
			.then(res => res.json())
			.then(data => {
				console.log(data);
				setInitializedPurchase(data);
				setBlocking(false);
			})
	}

	const renderItems = () => {
		return (
			<Box>
				{purchaseItems.map(i => (
					<Grid container columns={12} sx={{height: "36vh", maxHeight: "36vh"}}>
						<Grid xs={3}>
							<img style={{maxWidth: "50%", maxHeight: "80%"}} src={i.inventory.variant.mediaFiles[0].url} />
						</Grid>
						<Grid xs={1}>
						</Grid>
						<Grid xs={8} sx={{textAlign: "left"}}>
							<Typography>
								{i.inventory.variant.product.name}
							</Typography>
							<Typography>
								{i.inventory.variant.attributes.map(a => `${a.dimension.name}: ${a.value}`).join(" | ")}
							</Typography>
							<Typography>
								{`Quantity: ${i.amount}, for a price of $${i.inventory.price} for each`}
							</Typography>
						</Grid>
					</Grid>
				))}
			</Box>
		);
	}

	const renderContent = () => {
		// items
		if (activeStep === 0) {
			return <>
				{renderItems()}
			</>
		}
		// address
		else if (activeStep === 1) {
			return <>
				{addresses.length === 0 &&
					<>
						<Typography>You do not have an address yet</Typography>
						<Link to={"/account"}>Add an address</Link>
					</>
				}
				<Stack minHeight={"50vh"} direction={"column"} alignItems={"center"} justifyContent={"flex-start"}>
					{addresses.map(a => (
						<Box sx={{height: "5em"}}>
							<Stack direction={"row"} justifyContent={"space-evenly"} alignItems={"center"}>
								<Typography variant={"h6"}>{`${a.street}, ${a.city}, ${a.postalCode}, ${a.country}`}</Typography>
								<Box>
									<Button sx={{textTransform: "none"}} disabled={a.id === address} onClick={e => {
										e.preventDefault();
										setAddress(a.id);
										initializedPurchase.address = {id: a.id};
										setBlocking(true);
										fetch('/api/purchase/address',
											{headers: defaultHeaders, method: "PUT", body: JSON.stringify(a)})
											.then(res => {
												if (res.status === 200)
													refresh();
												else
													console.log('error in setting a new address')
											})
									}}>
										{a.id === address ? "Selected" : "Select this address"}
									</Button>
								</Box>
							</Stack>

						</Box>
					))}
				</Stack>
			</>
		}
		// review
		else if (activeStep === 2) {
			const a = addresses.find(i => i.id === address)!;
			return <>
				{renderItems()}
				<Box>
					<Typography>To be sent to this address: </Typography>
					<Typography>{`${a.street}, ${a.city}, ${a.postalCode}, ${a.country}`}</Typography>
				</Box>
			</>;
		}
		else {
			return (
				<Box>
					{orderError.length === 0 ?
						<Alert severity={"success"}>Order placed successfully.</Alert>
						:
						<Alert severity={"error"}>Your order cannot be processed. Reason: {orderError}</Alert>
					}
				</Box>
			);
		}
	}

	const renderButtons = () => {
		if (activeStep === 0) {
			return <>
				<Button onClick={e => {
					e.preventDefault();
					setBlocking(true);
					fetch('/api/purchase/cancel')
						.then(res => res.json())
						.finally(() => {
							setBlocking(false);
							navigate('/basket');
						})
				}}>
					Back to basket
				</Button>
				<Button disabled={blocking} onClick={e => {
					e.preventDefault();
					setActiveStep(cur => cur + 1);
				}}>
					Next to address
				</Button>
			</>;
		}
		else if (activeStep === 1) {
			return <>
				{/*TBD: blocking here because blocked when saving a new address on the fly*/}
				<Button disabled={blocking} onClick={e => {
					e.preventDefault();
					setActiveStep(cur => cur - 1);
				}}>
					Back to items
				</Button>
				<Button disabled={blocking || (address < 0)} onClick={e => {
					e.preventDefault();
					setActiveStep(cur => cur + 1);
				}}>
					Next to order view
				</Button>
			</>
		}
		else if (activeStep === 2) {
			return <>
				<Button onClick={e => {
					e.preventDefault();
					setActiveStep(cur => cur - 1);
				}}>
					Back to address
				</Button>
				<Button onClick={e => {
					e.preventDefault();
					setBlocking(true);
					fetch('/api/purchase/finalize', {headers: defaultHeaders, method: "GET"})
						.then(res => {
							if (res.status !== 200) {
								res.json().then(body => {
									setOrderError(body["reason"]);
								})
								console.log(`error in finalizing the purchase, code: ${res.status}`)
							}
							setBlocking(false);
						})
						.then(() => setActiveStep(cur => cur + 1))
				}}>
					Confirm
				</Button>
			</>
		}
		else {
			return <>
				<Button onClick={e => {
					e.preventDefault();
					navigate('/account');
				}}>
					Back to your account
				</Button>
			</>
		}
	}

	return (
		<Box margin={"2em"}>
			{/*Title*/}
			<Typography variant={"h6"}>Check out</Typography>

			{/*Step information*/}
			<Stepper style={{margin: "1em"}} activeStep={activeStep}>
				{steps.map((step, idx) => {
					return (
						<Step key={step}>
							<StepLabel>{step}</StepLabel>
						</Step>
					);
				})}
			</Stepper>

			{/*Content*/}
			<Container sx={{marginTop: "1em"}}>
				{renderContent()}
			</Container>

			{/*Buttons*/}
			<Box sx={{width: "100%", marginTop: "2em"}}>
				<Stack direction={"row"} alignItems={"center"} justifyContent={"space-around"} spacing={2}>
					{renderButtons()}
				</Stack>
			</Box>
		</Box>
	);
}