type DimensionAndAttributeType = {
	dimension: DimensionType,
	attributes: AttributeType[],
	onChange: (dimId: number, attr: AttributeType) => void
}
export default function Dimension({dimension, attributes, onChange}: DimensionAndAttributeType) {

	const [value, setValue] = useState<number>(-1);

	return (
		<FormControl size={"small"} sx={{ m: 1, minWidth: "8em" }}>
			<FormHelperText>Select {dimension.name}:</FormHelperText>
			<Select
				inputProps={{ 'aria-label': 'Without label' }}
				value={value === -1 ? '' : value}
				onChange={e => {
					const newValue = attributes.find(a => `${a.id}` === `${e.target.value}`);
					if (!newValue)
						return;
					setValue(newValue.id);
					onChange(dimension.id, newValue);
				}}
			>
				{
					attributes.map((a, i) => (<MenuItem value={a.id} key={i}>{a.value}</MenuItem>))
				}
			</Select>
		</FormControl>
	);
}