import {
	Box,
	Card,
	CardActionArea,
	CardContent,
	CardMedia,
	Stack,
	Typography
} from "@mui/material";
import {useNavigate} from "react-router";
import notFound from "../../404.webp";

export type ProductType = {
	id: number,
	name: string,
	description?: string,
	defaultPrice: number,
	forSaleFrom: string // as 2022-10-23T15:07:56.34191
	variants: VariantsType
}

export type VariantsType = {
	singular: boolean
	mediaFiles: {url: string}[]
}[]


const noImg = notFound;

export const findProperImage = (variants: VariantsType) => {
	let image_url = undefined;

	for (const variant of variants) {
		if (variant.mediaFiles.length === 0)
			continue;
		for (const media of variant.mediaFiles) {
			if (media.url.includes("404"))
				continue;
			image_url = media.url;
			break;
		}
		if (image_url !== undefined)
			break;
	}
	return image_url
}

export default function Product({id, name, defaultPrice, variants}: ProductType) {
	const navigate = useNavigate();

	let image_url = findProperImage(variants);

	if (image_url === undefined)
		image_url = noImg;

	return (
		<Card style={{margin: "0.8em", height: "24em"}} >
			<Stack direction={"column"} style={{height: "inherit"}} justifyContent={"space-between"}>
				<CardActionArea className={"action-area"} style={{height: "100%"}} onClick={e => {
					navigate(`/product/${id}`, {replace: false})
				}}>
					<CardMedia component={"img"} image={image_url} style={{height: "17em"}}>
					</CardMedia>
					<CardContent className={"card-content"} style={{height: "4em"}}>
						<Typography style={{
							display: "-webkit-box",
							width: "inherit",
							maxWidth: "inherit",
							overflow: "hidden",
							maxHeight: "6em",
							WebkitLineClamp: 3,
							WebkitBoxOrient: "vertical",
							textOverflow: "ellipsis"}}>
							{name}
						</Typography>
					</CardContent>
				</CardActionArea>
				<Box padding={"0.2em 1em"} height={"3em"}>
					<Typography gutterBottom={true} textAlign={"right"} style={{fontWeight: "bold", fontSize: "large"}}>
						{`$${defaultPrice.toFixed(2)}`}
					</Typography>
				</Box>
			</Stack>

		</Card>
	);
}