import {Box, FormControl, FormHelperText, MenuItem, Select, Typography} from "@mui/material";
import {useContext, useEffect, useState} from "react";
import {ProductContext} from "../ProductInfoWrapper";
import blue from "@mui/material/colors/blue";

type InventoryType = {
	inventoryId: number,
	setInventoryId: (newId: number) => void
}
export default function Inventory({inventoryId, setInventoryId}: InventoryType) {
	const {product, variant} = useContext(ProductContext);

	useEffect(() => {
		setInventoryId(-1);
	}, [variant]);

	const styles = {padding: "1em", maxWidth: "75%", margin: "auto"};

	if (variant === undefined) {
		const dims = product.dimensions!;
		setInventoryId(-1);
		return (
			<Box sx={styles}>
				<Typography variant={"h6"}>
					{`To check the inventory, please specify "${dims.map(d => d.name).join(" and ")}" first`}
				</Typography>
			</Box>
		);
	}

	if (variant.inventories === undefined || variant.inventories.length === 0) {
		setInventoryId(-1);
		return (
			<Box sx={styles}>
				<Typography variant={"h6"} color={"darkred"}>
					{`Sorry, this variant is out of stock.${product.variants!.length > 1 ? ` Check other options.` : ``}`}
				</Typography>
			</Box>
		)
	}

	const inventory = variant.inventories.find(i => i.id === inventoryId);
	const merchants = product.variants!.flatMap(v => v.inventories!.map(i => i.merchant));

	return (
		<Box sx={styles}>
			{inventory !== undefined ?
				<Typography variant={"h5"} fontWeight={"bold"} sx={{color: blue.A700}}>
					{`$${inventory!.price.toFixed(2)}`}
				</Typography>
				:
				<Typography variant={"h6"}>
					{`Choose a seller:`}
				</Typography>
			}

			<FormControl size={"small"} sx={{ m: 1, minWidth: "6em" }}>
				<FormHelperText>Select a seller:</FormHelperText>
				<Select
					inputProps={{ 'aria-label': 'Without label' }}
					value={inventoryId}
					onChange={e => {
						const newId = parseInt(`${e.target.value}`);
						setInventoryId(newId);
					}}
				>
					{
						variant.inventories.map((i, idx) =>
							<MenuItem value={i.id} key={idx}>
								{/*// @ts-ignore*/}
								{i.merchant?.brand || merchants.find(m => m.id && m.id === i.merchant).brand}
							</MenuItem>
						)
					}
				</Select>
			</FormControl>
		</Box>
	);
}