import {useContext, useEffect, useState} from "react";
import {AppContext, defaultHeaders} from "../../../App";
import {Box, Container, Typography} from "@mui/material";
import Order, {OrderType} from "./Order";
import Loading from "../../../util/Loading";

export default function OrderWrapper() {
	const {customer} = useContext(AppContext);
	const [orders, setOrders] = useState<OrderType[]>([]);
	const [loading, setLoading] = useState(true);

	useEffect(() => {
		fetch('/api/purchase/all', {headers: defaultHeaders})
			.then(res => res.json())
			.then(data => {
				setOrders(data);
			})
			.then(() => setLoading(false));
	}, [])

	if (customer === undefined)
		return <Typography>Not logged in</Typography>

	if (loading)
		return (
			<Container>
				<Loading></Loading>
			</Container>
		);

	return (
		<Container>
			{orders.length > 0 && orders.map(o => (
				<Order {...o}></Order>
			))}
			{orders.length === 0 &&
				<Typography sx={{margin: "1em"}} variant={"h6"}>You have not placed any order yet. </Typography>
			}
		</Container>
	);
}