import {useContext, useState} from "react";
import {AppContext, defaultHeaders, refreshCustomer} from "../../App";
import {Box, Button, TextField, Typography} from "@mui/material";
import {useNavigate} from "react-router";

export default function Login() {
	const {customer, setCustomer} = useContext(AppContext);
	const [email, setEmail] = useState("");
	const [password, setPassword] = useState("");
	const [error, setError] = useState(false);
	const navigate = useNavigate();

	if (customer !== undefined) {
		navigate("/account", {replace: true});
	}

	const styles = {width: "32vw", margin: "auto", paddingTop: "2em"}

	return (
		<Box sx={{width: "max-content", margin: "auto", paddingTop: "3em"}}>
			<Typography textAlign={"left"} variant={"h6"}>
				For security reasons, registration is disabled.
				Please use the information provided on the resume to log in.
			</Typography>
			<Typography variant={"h6"} color={"red"}>{`${error ? "Username and password incorrect!" : " "}`}</Typography>
			<Box sx={styles}>
				<TextField fullWidth
					required
					id="outlined-required"
					label="Email"
					value={email}
					onChange={e => setEmail(e.target.value)}
					error={error}
				/>
			</Box>
			<Box sx={styles}>
				<TextField fullWidth
					id="outlined-password-input"
					label="Password"
					type="password"
					autoComplete="current-password"
					value={password}
					onChange={e => setPassword(e.target.value)}
					error={error}
				/>
			</Box>
			<Box>
				<Button sx={{textTransform: "none", fontSize: "large"}} onClick={e => {
					e.preventDefault();
					const body = new URLSearchParams({
						'email': email,
						'password': password
					});
					fetch('/login',
						{headers: {'Content-Type': 'application/x-www-form-urlencoded;charset=UTF-8'},
						method: "POST", body: body, credentials: "include"})
						.then(res => {
							if (res.status === 200) {
								refreshCustomer(setCustomer,
									() => navigate('/account', {replace: true}));
							}
							else {
								setError(true);
							}
						})
						.catch(console.log)
				}}>Login</Button>
			</Box>
		</Box>
	);
}