import {useContext, useEffect, useState} from "react";
import {AppContext, defaultHeaders} from "../../../App";
import Loading from "../../../util/Loading";
import {Box, Divider, Typography} from "@mui/material";
import Address, {AddressType} from "./Address";

export default function AddressWrapper() {
	const {customer} = useContext(AppContext);
	const [loading, setLoading] = useState(true);
	const [addresses, setAddresses] = useState<AddressType[]>([]);
	const [edit, setEdit] = useState(0);

	useEffect(() => {
		if (customer === undefined) {
			setLoading(false);
		}
		fetch('/api/address', {headers: defaultHeaders, method: "GET"})
			.then(res => res.json())
			.then(data => setAddresses(data))
			.catch(console.log)
			.finally(() => setLoading(false))
	}, [edit]);

	if (customer === undefined)
		return <></>;

	if (loading)
		return <Loading height={"5vh"}></Loading>

	return (
		<Box>
			<Typography sx={{margin: "1em 5em 1em", textAlign: "left"}}>Add a new address:</Typography>
			<Address address={{id: -1, street: "", city: "", postalCode: "", country: ""}} old={false} setEdit={setEdit}></Address>
			<Divider color={"lightgray"}></Divider>
			<Typography sx={{margin: "1em 5em 1em", textAlign: "left"}}>Your addresses:</Typography>
			{addresses.length > 0 && addresses.map(a => <Address address={a} old={true} setEdit={setEdit}/>)}
			{addresses.length === 0 && (<Typography variant={"h5"}>No address yet.</Typography>)}
		</Box>
	);
}