import {useContext} from "react";
import {ProductContext} from "../ProductInfoWrapper";
import {Box, Typography} from "@mui/material";
import Paper from "@mui/material/Paper";
import {red, grey} from "@mui/material/colors";

// assuming at least one variant
export default function Pricing() {
	const {product, variant} = useContext(ProductContext);

	// if (singular) {
	// 	return (
	// 		<Paper elevation={0} sx={{marginRight: "0", marginBottom: "1em", width: "fit-content"}}>
	// 			<Typography variant={"h6"} textAlign={"left"} color={red.A700}>
	// 				{variant && `Reference price: $${variant.referencePrice}`}
	// 			</Typography>
	// 		</Paper>
	// 	);
	// }

	// should not happen, at least one variant per product
	if (!product.variants)
		return <></>

	const prices = product.variants.map(v => v.referencePrice).sort((a, b) => b - a);
	const cheap = prices[0];
	const pricy = prices[prices.length - 1];

	return (
		<Paper elevation={0} sx={{marginRight: "0", marginBottom: "1em", width: "fit-content"}}>
			<Typography variant={"h6"} textAlign={"left"} color={red["900"]}>
				{cheap === pricy ? `At $${cheap.toFixed(2)}` : `$${cheap.toFixed(2)} — $${pricy.toFixed(2)}`}
			</Typography>
			<Typography variant={"h6"} textAlign={"left"} color={grey.A700}>
				{variant ? `Reference price:` : 'Choose a variant'}
			</Typography>
			<Typography textAlign={"right"} color={red["900"]} variant={"h5"} fontWeight={"bold"}>
				{variant && `$${variant.referencePrice.toFixed(2)}`}
			</Typography>
		</Paper>
	);
}
