import {useContext, useEffect, useState} from "react";
import {AttributeType, ProductContext} from "../ProductInfoWrapper";
import {Box, Typography} from "@mui/material";
import Dimension from "./Dimension";
import Grid from "@mui/material/Unstable_Grid2";
import Loading from "../../../util/Loading";
import {defaultHeaders} from "../../../App";

// assuming that this product has some dimension, and some variant.
export default function DimensionWrapper() {
	const {product, setVariant} = useContext(ProductContext);
	const [loading, setLoading] = useState(true);

	// full mapping
	const [mapping, ] = useState<Map<number, AttributeType[]>>(new Map());
	// selected mapping
	const [selected, ] = useState<Map<number, number>>(new Map());
	const dims = product.dimensions!;

	// 1: size; 2: color
	// fetch all attributes of each dimension, variants might lack some attributes
	useEffect(() => {
		dims.forEach((dim, idx) => {
			fetch(`/api/attribute/dimension?dimension=${dim.id}`, {headers: defaultHeaders})
				.then(res => res.json())
				// to be replaced here: reference to real JSON sent from server
				.then(data => {
					mapping.set(dim.id, data);
				})
				.then(() => {
					if (idx + 1 === dims.length)
						setLoading(false);
				})
				.catch(console.log);
		});
	}, []);

	if (loading)
		return <Loading height={"5vh"}></Loading>

	const onChange = (dimId: number, attr: AttributeType) => {
		selected.set(dimId, attr.id);
		if (selected.size === mapping.size) {
			const attrs: Set<number> = new Set(selected.values());
			const result = product.variants!.filter(v => v.attributes?.every((a, ) => attrs.has(a.id)));
			if (result.length === 1) {
				setVariant(result[0]);
			}
			else {
				setVariant(undefined);
			}
		}
	}

	return (
		<Box paddingTop={"1em"}>
			<Typography textAlign={"right"} variant={"h6"} marginRight={"1em"}>Choose from:</Typography>
			<Grid container columns={12} justifyContent={"flex-end"}>
				{dims.map((dim) => (
					<Grid xs={3}>
						<Dimension dimension={dim}
								   attributes={mapping.get(dim.id)!}
								   onChange={onChange}
						/>
					</Grid>
				))}
			</Grid>
		</Box>
	);
}

























