import Link from "@mui/material/Link";
import {Box, Divider, Typography} from "@mui/material";
import "./Category.css"
import {useContext, useEffect, useState} from "react";
import {CatalogContext} from "../CatalogWrapper";
import {defaultHeaders} from "../../../App";

export type CategoryType = {
	id: number,
	parent: number | undefined,
	name: string
}

export const base: CategoryType = {
	id: 0, parent: undefined, name: "All"
}

export const bookBase: CategoryType = {
	id: 1, parent: undefined, name: "Books"
}

// set this finder by calling the API(have the serve return all categories arranged in trees) when first loading the app
// cur.id -> child[] -> child[] -> ... recursively
let finder: Map<number, CategoryType[]> = new Map([
	[0, [{id: 1, parent: 0, name: "book"}, {id: 2, parent: 0, name: "electronics"}]],
	[1, [{id: 3, parent: 1, name: "fiction"}, {id: 4, parent: 1, name: "non-fiction"}]],
	[2, [{id: 5, parent: 2, name: "laptop"}, {id: 6, parent: 2, name: "PC"}, {id: 7, parent: 2, name: "phones"}]],
	[3, [{id: 8, parent: 3, name: "romance"}, {id: 9, parent: 3, name: "adventure"}]]
])

export default function Category() {
	const [level, setLevel] = useState(0);
	const [parents, setParents] = useState<CategoryType[]>([]);
	const [current, setCurrent] = useState<CategoryType>(bookBase);
	const [children, setChildren] = useState<CategoryType[]>([]);
	const {category, onCategoryChange} = useContext(CatalogContext);

	useEffect(() => {
		finder.clear();
		fetch("/api/category/all", {headers: defaultHeaders})
			.then(res => res.json())
			.then((results: CategoryType[]) => {
				const recur = (id: number, children: any) => {
					if (children.length > 0)
						finder.set(id, children.map((e: any) => ({id: e.id, parent: id, name: e.name})));
					for (const cat of children) {
						// @ts-ignore
						recur(cat.id, cat.children);
					}
				}
				// recur(base.id, results);
				// @ts-ignore
				recur(bookBase.id, results[0].children)
				if (finder.has(current.id))
					setChildren(finder.get(current.id)!)
			})
			.catch(console.log);
	}, []);

	const buildPath = (lvl: number = 0) => {
		if (lvl === level)
			return (
				<Box paddingTop={"0.2em"} style={{marginLeft: `${lvl === 0 ? 0 : 1}em`, textAlign: 'left'}}>
					<Link style={{fontWeight: "bold"}} onClick={e => {
						e.preventDefault();
					}}>
						{`${current.name}`}{`${finder.has(current.id) ? ":" : ""}`}
					</Link>
				</Box>
			);
		const currentParent = parents[lvl]
		return (
			<Box paddingTop={"0.2em"} style={{marginLeft: `${lvl === 0 ? 0 : 1}em`, textAlign: 'left'}}>
				<Link className={"category-link"} underline={"hover"} onClick={e => {
					e.preventDefault();
					setLevel(lvl)
					setParents(parents.slice(0, lvl))
					setCurrent(currentParent)
				}}>
					{currentParent.name}
				</Link>
				{buildPath(lvl + 1)}
			</Box>
		);
	}

	useEffect(() => {
		if (finder.has(current.id))
			setChildren(finder.get(current.id)!)
		else
			setChildren([])
		onCategoryChange(current);
	}, [current])

	return (
		<Box margin={"1em 1em 0em"} padding={"0.5em"} className={"category"}>
			<Box className={"category-title"}><div>Categories</div></Box>
			<Divider style={{marginBottom: "2em"}}></Divider>
			{buildPath()}
			{
				children.map((cat, index) => {
					return (
						<Box paddingTop={"0.2em"} style={{marginLeft: `${(1 + level)}em`, textAlign: "left"}} key={index}>
							<Typography>
								<Link underline={"hover"}
									  className={"category-link"}
									  onClick={e => {
										  e.preventDefault();
										  setLevel(cur => cur + 1)
										  setParents(p => [...p, current])
										  setCurrent(cat)
									  }}>
									{`${cat.name}`}
								</Link>
							</Typography>
						</Box>
					);
				})
			}
		</Box>
	);
}