import {useContext, useEffect, useState} from "react";
import {Box, Container, Divider, Pagination, Stack} from "@mui/material";
import {ProductContext} from "../ProductInfoWrapper";
import Grid from "@mui/material/Unstable_Grid2";
import notFound from "../../../404.webp";

const noImg = notFound;

export default function Showcase() {
	const {variant, product} = useContext(ProductContext);
	const [currentImg, setCurrentImg] = useState(0);

	useEffect(() => setCurrentImg(0), [variant]);

	let currentVariant = variant || product.variants![0];
	const imgs = currentVariant.mediaFiles.map(m => m.url.includes("404") ? noImg : m.url);
	const absent = (imgs.length === 0);

	return (
		<Container sx={{height: "75vh", maxHeight: "75vh"}}>
			<Grid container columns={16} height={"50vh"} maxHeight={"55vh"}>
				<Grid xs={1}><Box></Box></Grid>
				<Grid xs={14}>
					<Container sx={{width: "100%", height: "100%"}}>
						<Stack sx={{width: "100%", height: "100%"}} direction={"column"} justifyContent={"center"} alignItems={"center"}>
							<img style={{maxWidth: "100%", maxHeight: "50vh"}}
								 src={absent ? noImg : imgs[currentImg]} />
						</Stack>
					</Container>
				</Grid>
				<Grid xs={1}><Box></Box></Grid>
			</Grid>
			{imgs.length > 1 && (
				<>
					<Divider sx={{margin: "2vh 1em"}}></Divider>
					<Container>
						<Pagination sx={{width: "fit-content", margin: "auto"}} page={currentImg + 1}
									onChange={(e, value) => {
										console.log(`page changed to ${value}`)
										setCurrentImg(value-1);
									}}
									size={"small"}
									boundaryCount={2}
									count={imgs.length}/>
					</Container>
				</>
				)
			}
		</Container>
	);
}