import * as React from 'react';
import Button from '@mui/material/Button';
import Menu from '@mui/material/Menu';
import MenuItem from '@mui/material/MenuItem';
import {Box} from "@mui/material";
import {Link as GoTo} from "react-router-dom";
import Link from "@mui/material/Link";

export type MenuItemType = {name: string, link: string};
export type SubMenuItemType = {name: string, link: string};
export type NavigationBarType = {
	menu: MenuItemType
	subMenu?: SubMenuItemType[]
	login: boolean
}

export default function NavigationBar({menu, subMenu}: NavigationBarType) {
	const [anchorEl, setAnchorEl] = React.useState<null | HTMLElement>(null);
	const open = Boolean(anchorEl);
	const handleClick = (event: React.MouseEvent<HTMLButtonElement>) => {
		setAnchorEl(event.currentTarget);
	};
	const handleClose = () => {
		setAnchorEl(null);
	};

	return (
		<Box>
			<Button sx={{fontWeight: "bold", textTransform: "none", padding: "0.2em"}} size={"large"}
				id="basic-button"
				aria-controls={open ? 'basic-menu' : undefined}
				aria-haspopup="true"
				aria-expanded={open ? 'true' : undefined}
				onClick={subMenu ? handleClick : x => x}
			>
				{menu.link.startsWith("http") ?
					<Link underline={"hover"}
						  sx={{textDecoration: "none"}}
						  color={"secondary"}
						  fontSize={"large"}
						  target={"_blank"}
						  rel={"noopener"}
						  href={menu.link}>
						{menu.name}
					</Link>
					:
					<Link color={"secondary"} underline={"hover"} fontSize={"large"}>
						<GoTo style={{textDecoration: "none"}} to={menu.link} >{menu.name}</GoTo>
					</Link>
				}
			</Button>
			{subMenu && <Menu
				id="basic-menu"
				anchorEl={anchorEl}
				open={open}
				onClose={handleClose}
				MenuListProps={{
					'aria-labelledby': 'basic-button',
				}}
			>
				{subMenu.map(e => <MenuItem onClick={handleClose}><Link underline={"hover"} href={e.link}>{e.name}</Link></MenuItem>)}
			</Menu>}
		</Box>
	);
}